import { mapState } from 'vuex'; 
import postCard from '@/components/postCard';
export default {
	components: {
		postCard
	},
	data() {
		return {
			myObjectId: '',	// 我的id
			userObjectId: '',	// 用户Id
			userList: [],	// 用户列表
			pageSetting: {
				pageIndex: 1,	// 页码
				pageSize: 10,	// 每页页数
				totalPage: 1,	// 总页数
				totalSize: 0,	// 总条数数
			},
		}
	},
	computed: {
		...mapState(['hasLogin','userInfo'])
	},
	async onLoad(option) {
		let that = this;
		that.userObjectId = option.objectId ? option.objectId : '17320dda35' ;
		uni.getStorage({
		    key: 'userInfo',
		    success: function (res) {
				that.myObjectId = res.data.objectId;
		    }
		});
	},
	onReady(){
		this.getUserList();
	},
	// 分享
	onShareAppMessage() {
		return {
			title: '事事通',
			path: '/pages/index/index'
		}
	},
	// 到底
	onReachBottom(){
		if (this.pageSetting.pageIndex <= this.pageSetting.totalPage) {
			//设置列表数据
			this.getUserList()
		}
	},
	methods: {
		// 详情、结果
		navToDetails(id){
			uni.navigateTo({
				url: '/pages/post/postDetail?postId=' + id
			})
		},
		// 获取用户列表
		getUserList() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			var query = that.Bmob.Query('_User');
			query.limit(10);	// 每页条数
			query.skip(10 * (that.pageSetting.pageIndex - 1));	// 分页查询// 对score字段降序排列
			query.equalTo("objectId", "!=", that.myObjectId);
			query.count().then(res => {
				if(res.count === 0){
					that.userList = []
				}else{
					that.pageSetting.totalPage = parseInt(res.count/that.pageSetting.pageSize) + 1
					query.find().then(res => {
						uni.hideLoading();
						that.pageSetting.pageIndex += 1;
						that.userList = that.userList.concat(res);
					});
				}
			});
		},
	}
}
